*{
    box-sizing: border-box;
}
.searchbox{
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.01), 0px 3px 18px rgba(0, 0, 0, 0.12);
    width: 100%;
    padding: 0.38rem 0;
    
    .am-search{
        width: 100%;
        padding: 0 0.3rem;
        background-color: #fff;
        box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.01), 0px 3px 18px rgba(0, 0, 0, 0.12);
        border-radius: 5px;
        .am-search-cancel{
            color: #545454;
            font-size: 0.28rem;
        }
        .am-search-synthetic-ph-placeholder{
            color: #888;
            font-weight: bold;
            font-size: 0.32rem;
        }
    }
    
}
.searchList{
    margin-top: 0.8rem;
    .history{
        font-size: 0.26rem;
        line-height: 0.65rem;
    }
    .searchtabs{
        margin-top: 0.6rem;
        font-size: 0.26rem;
        .title{
            margin-bottom: 0.55rem;        
            .on{
                color: rgb(0, 132, 137);
                padding-bottom: 0.2rem;
                border-bottom: 2px solid rgb(0, 132, 137);
            }
            .right{
                margin-left: 0.5rem;
            }
        }
    }
}

.am-tag{
    margin: 0.1rem 0.2rem 0.1rem 0;
}
